import React from "react";
import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import {Link} from "react-scroll"

function Header() {
  return (
    <Navbar className="navBar" collapseOnSelect fixed="top" expand="lg" variant="light">
      <Container>
        <Navbar.Brand href="/">Clover</Navbar.Brand>
        <Navbar.Toggle aria-controls="responsive-navbar-nav" />
        <Navbar.Collapse id="responsive-navbar-nav">
          <Nav className="me-auto" style={{marginLeft: 20}}>
            <Nav.Link href="/" className="headerLink">Home</Nav.Link>
             <Nav.Link>
             <Link to="Price" className="headerLink" activeClass="active" spy={true} smooth={true} offset={-75} duration={700}>Price</Link>
             </Nav.Link>
            <Nav.Link>
             <Link to="Map" className="headerLink" activeClass="active" spy={true} smooth={true} offset={-75} duration={700}>Map</Link> 
            </Nav.Link>
            <Nav.Link>
            <Link to="Contact" className="headerLink" activeClass="active" spy={true} smooth={true} offset={-75} duration={700}>Contact</Link>
            </Nav.Link>
          </Nav>
        </Navbar.Collapse>
      </Container>
    </Navbar>
  );
}

export default Header;
