import React from "react";
import Carousel from 'react-bootstrap/Carousel';
import img1 from "../images/room-img-1.jpg"
import img2 from "../images/room-img-2.jpg"
import img3 from "../images/room-img-3.jpg"

function CloverCarousel() {
  return (
    <Carousel className="clover-carousel" variant="dark">
      <Carousel.Item>
        <img
          
          className="d-block w-100"
          src={img1}
          alt="First slide"
        />
      </Carousel.Item>
      <Carousel.Item>
        <img
          className="d-block w-100"
          src={img2}
          alt="Second slide"
        />
      </Carousel.Item>
      <Carousel.Item>
        <img
          className="d-block w-100"
          src={img3}
          alt="Third slide"
        />
      </Carousel.Item>
    </Carousel>
  );
}

export default CloverCarousel;