import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';

function CloverTable() {
  return (
    <Container id="Price">
      <header className='tableHead' ><h1>Price</h1></header>
      <Row className="tableContent">
        <Col>Cut</Col>
        <Col>3,000 yen</Col>
      </Row>
      <Row  className="tableContent">
        <Col>Color</Col>
        <Col>5,000 yen</Col>
      </Row>
      <Row  className="tableContent">
        <Col>Perm</Col>
        <Col>7,000 yen</Col>
      </Row>
     
    </Container>
  );
}

export default CloverTable;