import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import GoogleMapReact from 'google-map-react';

const AnyReactComponent = ({ text }) => <div>{text}</div>;


function CloverMap() {

  const defaultProps = {
    center: {
      lat: 35.271457,
      lng: 139.15071
    },
    zoom: 15
  };


  return (
    <Container className="mapContainer" id="Map">
      <header className='tableHead'><h1>Where We Are </h1></header>
      <Row className="tableContent">
       
        <Col>
        <div className="mapBox" style={{ height: '500px', width: '400px', backgroundColor: "green" }}>
          <p>This is a map</p>
      
    </div>
           
        </Col>
        <Col className='mapDescription'>
           <p>Parking Area x 2</p>
           <p>304-2, 1F, Kuno, Odawarashi, </p>
           <p>Link here <a href='https://goo.gl/maps/T81p6WpY9xxEgzHH8'>map</a></p>
        
        </Col>
      </Row>
     
     
    </Container>
  );
}

export default CloverMap;

